.main-nav {
  position: absolute;
  width: auto;
  height: 56px;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 2000;
}

.main-nav__toggle {
  border: 0;
  background: none;
  float: right;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav__toggle::before {
  content: "";
  display: block;
  width: 22px;
  height: 22px;
  position: absolute;
  top: 16px;
  right: 20px;
  z-index: 200;
}

.main-nav--opened .main-nav__toggle::before {
  background-image: url("../img/icon-cross.svg");
}

.main-nav--opened .main-nav__toggle:hover::before {
  background-image: url("../img/icon-cross-hover.svg");
}

.main-nav--opened .main-nav__toggle:active::before {
  background-image: url("../img/icon-cross-active.svg");
}

.main-nav--closed .main-nav__toggle::before {
  background-image: url("../img/icon-menu.svg");
}

.main-nav--closed .main-nav__toggle:hover::before {
  background-image: url("../img/icon-menu-hover.svg");
}

.main-nav--closed .main-nav__toggle:active::before {
  background-image: url("../img/icon-menu-active.svg");
}

@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
}
